// .info-container
//     background: linear-gradient(to top,rgba(0,0,0,0),rgba(0,0,0,0.5)) !important

.main-info {
    font-size: 1.2rem;
    color: white;
    @include centered;
    flex-direction: column; }

.main-info span {
    font-size: 1rem; }

#current-temp {
    color: white;
    font-size: 5rem; }

#main-header {
    padding-top: 67px !important; }

#main-header-info {
    text-align: center;
    color: white;
    transition: all 0.2s;
    // Night
    background: #19235c93 !important;
 }    // background: #19435c93 !important

#main-header-info:hover {
    background: rgba(25, 35, 92, 0.9) !important; }
#weather-input {
    color: white;
    padding: 0.3rem;
    background: #222a55c0 !important;
    border: none; }

#weather-input placeholder {
    color: white; }

#weather-input:focus {}

