.sub-info-list li {
    margin: 20px 25px; }

.days-info-container {
    min-height: 100%;
    color: white; }

.days-info-container .element {
    height: 10rem;
    padding-top: 1.2rem; }

.days-info-container .element:hover {
    background: #3b3b3b3a; }
