

// Project content

.categories a:hover {
    text-decoration: none; }

.project-content {
  position: relative;
  transition: all 0.2s ease-in-out; }

.project-content:hover {
  transform: scale(1.02);
  opacity: 1;
  // z-index: 9999
  .content-overlay {
    opacity: 1; } }

.categories h1 {
  text-shadow: 0px 5px 15px 1px black; }

.project-content img {
  background-color: white;
  width: 100%;
  transition: all 0.3s ease-in-out;
  border: 1px solid white;
  box-shadow: 0px 5px 15px 1px black;
  border-radius: 15px;
  opacity: .9; }

.project-content:hover .content-details {
  top: 50%;
  left: 50%;
  opacity: 1; }

.content-details {
  @include absoluteCentered(50%, 60%);
  padding-left: 1em;
  padding-right: 1em;
  width: 100%;
  font-size: .8rem;
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out 0s;
  -moz-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s; }

.fadeIn-bottom {
  top: 80%; }

.icons {
  padding-right: 5px; }

.icon-laptop {
  font-size: 2rem; }

.content-overlay {
  border-radius: 15px;
  background: rgba(0,0,0,0.7);
  position: absolute;
  height: 99;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  -webkit-transition: all 0.4s ease-in-out 0s;
  -moz-transition: all 0.4s ease-in-out 0s;
  transition: all 0.4s ease-in-out 0s; }

.content-details h3 {
  color: #fff;
  font-weight: 500;
  letter-spacing: 0.15em;
  margin-bottom: 0.5em;
  text-transform: uppercase; }

.overlay-text {
  @include absoluteCentered(50%, 60%);
  color: white;
  font-size: 20px;
  opacity: 1; }

.project_list {
  transition: all 0.5s;
  opacity: 0.5;
 }  // CATEGORIES


// CATEGORIES

