.categories {
  background: $categories-background; }

.categories-list {
  display: flex;
  list-style: none; }

.categories-list li {
  padding: 9px 5px; }


.categories-list li a {
  text-shadow: 2px 2px 2px #9b9b9b8a;
  text-decoration: none;
  padding: 10px 5px;
  color: grey;
  opacity: 0.5;
  font-weight: 600;
  font-size: .8rem;
  transition: all 0.3s ease 0s; }


.categories-list li a:hover {
  font-size: .9rem; }

.categories-list li:nth-child(4)::after {
  content: ""; }

.categories-list li::after {
  content: "/";
  margin: 0 0 0 10px;
  opacity: .3; }

.categories-list li a:hover {
  opacity: 1;
  list-style: none;
  border: none; }

.active {
  opacity: 1 !important; }

code {
  font-size: 100%; }

// ABOUT SECTION

.contact-btn a {
  color: white !important; }

.contact-btn {
