.skills-list div {
  border-radius: 1rem;
  background: #494949;
  text-align: center;
  color: white;
  font-weight: 500; }

.aboutContainer {
  background: $about-backgroud;
  h5,h2 {
    font-weight: 800; }
  h1 {
    font-weight: 600;
    text-transform: uppercase;
    margin: 0 15px;
    font-size: 2.5rem; }
  h5 {
    font-weight: 600;
    text-transform: uppercase; } }

/* LEARN MORE */

#learn-more {
  color: #001529; }

#learn-more button {
  outline: none;
  border: none;
  padding: .5rem 0;
  transition: all 0.2s;
  color: black; }

#learn-more button:hover {
  background: $darker-blue;
  color: white; }

#learn-more span {
  position: absolute;
  right: 0;
  margin-right: 1rem;
  font-size: 1.5rem; }

